






























import { Component, Vue, Watch } from "vue-property-decorator";
import { getResourceCategory } from "../../../api/system/index2";
@Component({
  components: {},
})
export default class UserResources extends Vue {
  filterText: any = "";
  dataTree: any = [];
  defaultProps: any = {
    children: "children",
    label: "name",
  };
  @Watch("filterText")
  filterTextWatch(val: any) {
    console.log(val);
    (this.$refs.tree as any).filter(val);
  }
  filterNode(value: any, data: any) {
    if (!value) return true;
    return data[this.defaultProps.label].indexOf(value) !== -1;
  }

  currentChange(item: any) {
    console.log(item);
  }
  async created() {
    const res: any = await getResourceCategory();
    console.log(res);
    this.dataTree = this.$tool.listToGruop(res, { rootId: 0 });
  }
}
